import { Component, OnInit } from '@angular/core';
import { Book } from '../book';
import { BookServiceService } from '../book-service.service';

@Component({
  selector: 'app-home-list',
  template: `
    <div class="row">

      <div class="col-12 col-md-8">

        <div class="card" *ngFor="let book of books">
          <div class="card-block">
            <img src="http://localhost:3000/images/{{ book.image }}" width="100" height="120" style="float:left; margin-right:50px">
            <h4><a href="/book/{{ book._id }}">{{ book.name }}</a><small>&nbsp;&nbsp;
              <i class="fas fa-star"></i><i class="fas fa-star"></i><i class="fas fa-star"></i><i class="far fa-star"></i><i class="far fa-star"></i>
            </small></h4><p class="address">{{ book.desc }}</p><div class="facilities">

            <span class="badge badge-warning"  *ngFor="let types of book.type">{{ types }} </span>
          </div></div></div>

      </div>
    </div>
  `,
  styleUrls: ['./home-list.component.css']
})
export class HomeListComponent implements OnInit {

  books: Book[]

  constructor(private bookService: BookServiceService) { }

  ngOnInit() {
    this.bookService
      .getBooks()
      .then((books: Book[]) => {
        this.books=books.map(book=>{return book;})
      })
  }

}
